import { Injectable } from '@angular/core';
import {Router} from "@angular/router";
import {HttpClient} from "@angular/common/http";
import * as jwt_decode  from 'jwt-decode';

import { environment } from '../../environments/environment';


export class auth {
  sub: string;
  roles:any;
}
@Injectable()

export class AuthentificationService {
  public name=null;
  public host = "http://localhost:8070/";
  public jwtToken=null;
  public role:Array<any>;
  public roles:Array<any>;
  public auth:auth;
  public size;

  constructor(private http: HttpClient,private router:Router) {

  }
  login(data) {
    return this.http.post<any>( environment.apiUrl+"login", data, {observe: 'response'});

  }
  saveToken(jwt:string)
  {  if(jwt)
  {
    this.jwtToken=jwt;
    localStorage.setItem('token',jwt);
    this.auth=jwt_decode(this.jwtToken);
    console.log(this.auth);

    this.role=this.auth.roles;
    this.name=this.auth.sub;

    this.role=this.auth.roles;

  }

  }
  loadToken()
  {
    this.jwtToken=localStorage.getItem('token');
    if(this.jwtToken)
    {
      this.auth=jwt_decode(this.jwtToken);
      this.name=this.auth.sub;
      this.role=this.auth.roles;


      return this.name;
    }

  }

  loadpero()
  {let perso;
    perso=localStorage.getItem('perso');
    console.log(perso)
    if(perso)
    {


      return perso;
    }else
    return null;
  }


  logout()
  {
    localStorage.removeItem('token');
    this.jwtToken=null;
    this.name=null;


   // this.router.navigateByUrl('');




  }
  isAdmin()
  {
    for (let r of this.role)
    {
      if(r=='ADMIN')
        return true;
    }
    return false;
  }

  getTokens()
  {

    this.jwtToken=localStorage.getItem('token');
    if(this.jwtToken)
    {
      this.auth=jwt_decode(this.jwtToken);
      this.name=this.auth.sub,
        this.roles=this.auth.roles;



      return this.jwtToken;
    }

  }

}
