import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import {ReactiveFormsModule, FormsModule} from '@angular/forms'
import {MatDialogModule,MatDialog} from '@angular/material/dialog';

import {MatChipsModule} from '@angular/material/chips';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { InscriptionComponent } from './inscription/inscription.component';
import { QuizComponent  } from './quiz/quiz.component';
import { RouterModule, Routes } from '@angular/router';
import { StartComponent } from './start/start.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AdminComponent } from './admin/admin.component';
import {MatTableModule} from '@angular/material';
import {MatInputModule} from '@angular/material';
import {MatFormFieldModule} from '@angular/material';
import { FinComponent } from './fin/fin.component';
import {HTTP_INTERCEPTORS, HttpClientModule} from '@angular/common/http';
import { QuizService } from './services/quiz.service';
import { AdminService } from './services/admin.service';
import {MatPaginatorModule} from '@angular/material/paginator';
import { InscriptionService } from './services/inscription.service';
import { ResultatComponent } from './resultat/resultat.component';

import { ChartsModule } from 'ng2-charts';
import { CommentaireComponent } from './commentaire/commentaire.component';

import {MatCardModule} from '@angular/material/card'

import {MatSelectModule} from '@angular/material/select';

import {MatButtonModule} from '@angular/material/button';
import {MatTooltipModule} from '@angular/material/tooltip';

import {MatCheckboxModule} from '@angular/material/checkbox';
import { LoginComponent } from './login/login.component';
import {AuthentificationService} from "./services/authentification.service";
import { FullComponent } from './full/full.component';
import {ActiveService} from "./services/active.service";
import {IntercepteurService} from "./services/intercepteur.service";
import { MenuComponent } from './menu/menu.component';
import {MatMenuModule} from '@angular/material/menu';
import {ActiveloginService} from "./services/activelogin.service";
import {IsadminService} from "./services/isadmin.service";
import { CountdownModule ,CountdownGlobalConfig} from 'ngx-countdown-2';
import {ActivpersoService} from "./services/activperso.service";
import { DialogOvervieComponent } from './dialog-overvie/dialog-overvie.component';
import {MatGridListModule} from '@angular/material/grid-list';
import {MatExpansionModule} from '@angular/material/expansion';

const appRoutes: Routes = [

  { path: 'quiz', component: QuizComponent ,canActivate: [ActivpersoService]},
  { path: 'inscription', component: InscriptionComponent ,canActivate: [ActiveService]},
  { path: '', component: StartComponent ,canActivate: [ActiveService],pathMatch:"full"},

  { path: 'admin', component: AdminComponent ,canActivate: [IsadminService]},
  { path: 'fin', component: FinComponent ,canActivate: [ActiveService]},
  { path: 'login', component: LoginComponent  ,canActivate:  [ActiveloginService]},
  { path: 'resultat', component: ResultatComponent ,canActivate: [ActiveService] },
  { path: 'resultat/:id', component: ResultatComponent ,canActivate: [ActiveService]},
];


function countdownConfigFactory(): CountdownGlobalConfig {
   return{ format: `mm:ss` };
}

@NgModule({
  declarations: [
    AppComponent,
    InscriptionComponent,
    QuizComponent,
    StartComponent,
    AdminComponent,
    FinComponent,
    ResultatComponent,
    CommentaireComponent,
    LoginComponent,
    FullComponent,
    MenuComponent,
    DialogOvervieComponent,



  ],
  imports: [

    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    ),
    RouterModule.forRoot(appRoutes,{useHash : true}),
    BrowserModule,
    AppRoutingModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    MatTableModule,
    MatInputModule,
    MatFormFieldModule,
    FormsModule,
    MatPaginatorModule,
    HttpClientModule,
    ChartsModule,
    MatCardModule,
    MatSelectModule,
    MatButtonModule,
    MatTooltipModule,
    MatCheckboxModule,
    MatMenuModule,
    CountdownModule,
    MatChipsModule,
    MatDialogModule,
    MatGridListModule,
    MatExpansionModule
  ],
  providers: [

    { provide: CountdownGlobalConfig, useFactory: countdownConfigFactory },
    { provide: HTTP_INTERCEPTORS, useClass:IntercepteurService, multi: true },

    AdminService,InscriptionService,QuizService,AuthentificationService,ActiveService,ActiveloginService,IsadminService,ActivpersoService],
  bootstrap: [AppComponent],
  entryComponents: [DialogOvervieComponent],

})
export class AppModule { }

