import {Injectable} from "@angular/core";
import {
  HttpErrorResponse,
  HttpEvent,
  HttpHandler,
  HttpHeaders,
  HttpInterceptor,
  HttpRequest,
  HttpResponse
} from "@angular/common/http";
import {Observable, throwError} from "rxjs";

import {catchError, finalize, map} from "rxjs/operators";
import {AuthentificationService} from "./authentification.service";
import {Router} from "@angular/router";
@Injectable()
export class IntercepteurService  implements HttpInterceptor {

  requestCount: number = 0;

  constructor(private authService: AuthentificationService ,private router:Router) {
  }

  intercept(req: HttpRequest<any>,
            next: HttpHandler): Observable<HttpEvent<any>> {

    this.requestInterceptor();

    let headers: HttpHeaders = req.headers;

    let token = this.authService.getTokens();

    if (token) {
      req = req.clone({ headers: req.headers.set('Authorization', token) });

    }

    return next.handle(req).pipe(
      map((event: HttpEvent<any>) => {
        if (event instanceof HttpResponse) {
          this.onSubscribeSuccess(event);
        }
        return event;
      }),
      catchError((response: HttpErrorResponse) => {


        let errorData = {};
        let error = response.error;
        errorData = {
          status: response.status,
          message: (error.message ? error.message : 'Erreur inconnue'),
          code: (error.error ? error.error.code : 0)
        };
        this.onSubscribeError(errorData);
        return throwError(errorData);
        console.log("catchError",errorData)

      })
      , finalize(() => {
        this.onFinally();
      })
    );
  }

  private onSubscribeSuccess(res: HttpResponse<any>): void {


    console.log("onSubscribeSuccess",res)


    this.responseInterceptor()
  }

  private onSubscribeError(error: any): void {

    if(error.status==403 && error.message=="Access Denied")
    {
      console.log("onSubscribeError",error)
      console.log("onSubscribeError",error.status)
      this.authService.logout();
      this.router.navigateByUrl("login");
    }

  }

  /**
   * onFinally
   */
  private onFinally(): void {
    // this.responseInterceptor();
  }


  /**
   * Request interceptor.
   */
  private requestInterceptor(): void {
    this.requestCount++;
    // if(this.requestCount === 1) {
    //  this.loaderService.show();
    // }
  }

  /**
   * Response interceptor.
   */
  private responseInterceptor(): void {
    this.requestCount--;
    // if(this.requestCount === 0) {
    // this.loaderService.hide();
    // }
  }
  gethttptoken()
  {

  }
}
