import { Component, OnInit, ViewChild } from '@angular/core';
import {MatTableDataSource} from '@angular/material/table';
import { AdminService } from '../services/admin.service';
import {MatPaginator} from '@angular/material/paginator';
import { Router } from '@angular/router';
import {CommentaireComponent} from "../commentaire/commentaire.component";
import {AuthentificationService} from "../services/authentification.service";




@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.scss']
})

export class AdminComponent implements OnInit {
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;


  @ViewChild(CommentaireComponent,{static: false})
  private commentaireComponent:CommentaireComponent;


  com :any
  comment :any =' ';
  personnel :any
  displayedColumns: string[] = ['id', 'nom','prenom', 'niveau','Specialite','Experience','date','poste','dure','symbol'];
  dataSource : MatTableDataSource<any>;
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  constructor(public AdminService: AdminService,public router:Router,public auth:AuthentificationService) { }
  ngOnInit() {

    if(this.auth.loadpero()!=null)
    {
      localStorage.removeItem('perso');
    }


    this.AdminService.findAll().subscribe(data => {
     this.personnel=data;
     console.log(data);
     this.dataSource = new MatTableDataSource(this.personnel);
     this.dataSource.paginator = this.paginator;

     });
  }
  affResult(data){

    console.log(data);
    this.router.navigateByUrl("resultat/"+data);

  }

  commentaire(id,data){

    console.log(data.commentaire);

    console.log(id);
    this.AdminService.commentaire(id,data.commentaire).subscribe(data => {
  this.comment='';
      console.log(data);

    });

  }

  view(id){


    console.log(id);
    console.log("oui");
 this.commentaireComponent.com(id);

  }
}
