import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from '../../environments/environment';

@Injectable()
export class AdminService {



  private contact: string;
  constructor(public http:HttpClient){
  }

  public findAll(){
      return this.http.get(environment.apiUrl+'personnel');
    }

  public findResult(id){
    return this.http.get(environment.apiUrl+'personnel/'+id);
  }

  public commentaire(id,com){
    return this.http.post<any>(environment.apiUrl+'commentaire/'+id,com);
  }
}
