import {Component, Inject, OnInit} from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {Echelles} from "../models/Echelles";

@Component({
  selector: 'app-dialog-overvie',
  templateUrl: './dialog-overvie.component.html',
  styleUrls: ['./dialog-overvie.component.scss']
})



export class DialogOvervieComponent  {

  constructor(
    public dialogRef: MatDialogRef<DialogOvervieComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Echelles) {}

  onNoClick(): void {
    this.dialogRef.close();
  }
}
export interface DialogData {

  name: any;
}
