import {Component, Input, OnInit} from '@angular/core';
import {AdminService} from "../services/admin.service";
import {Router} from "@angular/router";
import {AuthentificationService} from "../services/authentification.service";


export class data {
  commentaire: string;
   user:string;
}
@Component({
  selector: 'app-commentaire',
  templateUrl: './commentaire.component.html',
  styleUrls: ['./commentaire.component.scss']
})
export class CommentaireComponent implements OnInit {

  id
   comm
  commentaire
  d:data =new data();
  constructor(public authservice: AuthentificationService,public AdminService: AdminService,public router:Router) { }

  ngOnInit() {




  }
  com(id) {
    console.log(id);
   this.id=id;

    this.AdminService.findResult(this.id).subscribe(data => {
      this.comm=data[0]["commentaire"];
      console.log(data);
      console.log(this.comm);

    });
  }

  commentairee(id){

    console.log(this.commentaire);

    console.log(id);
    this.d.commentaire=this.commentaire;
    this.d.user=this.authservice.loadToken();
    console.log(this.d);
    this.AdminService.commentaire(id,this.d).subscribe(data => {
      this.commentaire="";
      this.com(id);
      console.log(data);

    });

  }

}
