import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';

@Injectable()
export class QuizService {

  constructor(private http: HttpClient) { }

  get(url: string) {
    return this.http.get(url);
  }

  getAll() {
    return [
      { id: 'data/javascript.json', name: 'JavaScript' },

    ];
  }

  public saveResultat(resultat,id){
    return this.http.post<any>(environment.apiUrl+'resultat/'+id,resultat);
  }
  public saveReponses(data,id){
    return this.http.post<any>(environment.apiUrl+'reponses/'+id,data);
  }

  public test(data){
    return this.http.post<any>(environment.apiUrl+'test/',data);
  }
}
